// –§—É–Ω–∫—Ü—ñ—ó –¥–ª—è —Ä–æ–±–æ—Ç–∏ –∑ –±–µ–∫–µ–Ω–¥–æ–º
import axios from 'axios';
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∫–∞—Ç–µ–≥–æ—Ä—ñ–π –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
export async function fetchProductCategories() {
    const API_URL_CATEGORY = 'https://dummyjson.com/products/category-list';
  try {
    const response = await axios.get(API_URL_CATEGORY);
    return response.data; // –ø–æ–≤–µ—Ä—Ç–∞—î –º–∞—Å–∏–≤ –∫–∞—Ç–µ–≥–æ—Ä—ñ–π
  } catch (error) {
    console.error('–ü–æ–º–∏–ª–∫–∞ –ø—ñ–¥ —á–∞—Å –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∫–∞—Ç–µ–≥–æ—Ä—ñ–π:', error.message);
    throw error;
  }
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑–∞ –∫–∞—Ç–µ–≥–æ—Ä—ñ—î—é
export async function fetchProductsByCategory(category) {
    // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ –ø–æ—Ç–æ—á–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É (—Å—Ç–∞—Ç–∏—á–Ω–æ ‚Äî –∑–∞–≤–∂–¥–∏ 1)
    const currentPage = 1;

    // –§–æ—Ä–º—É—î–º–æ URL –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è 12 –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑—ñ –∑–º—ñ—â–µ–Ω–Ω—è–º –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ –¥–æ —Å—Ç–æ—Ä—ñ–Ω–∫–∏ (skip = 0)
    const API_URL_PRODUCTS = `https://dummyjson.com/products/category/${category}?limit=12&skip=${(currentPage - 1) * 12}`;

    try {
        // –í–∏–∫–æ–Ω—É—î–º–æ GET-–∑–∞–ø–∏—Ç –¥–æ API
        const response = await axios.get(API_URL_PRODUCTS);

        // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ —Ç—ñ–ª—å–∫–∏ –º–∞—Å–∏–≤ –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ —ñ–∑ –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ
        return response.data.products;

    } catch (error) {
        // –Ø–∫—â–æ —Å—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ ‚Äî –≤–∏–≤–æ–¥–∏–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
        console.error('–ü–æ–º–∏–ª–∫–∞ –ø—ñ–¥ —á–∞—Å –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤:', error.message);

        // –ü–µ—Ä–µ–¥–∞—î–º–æ –ø–æ–º–∏–ª–∫—É –¥–∞–ª—ñ
        throw error;
    }
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –≤—Å—ñ—Ö –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
export async function fetchAllProducts() {
    // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ –ø–æ—Ç–æ—á–Ω—É —Å—Ç–æ—Ä—ñ–Ω–∫—É (—Å—Ç–∞—Ç–∏—á–Ω–æ ‚Äî –∑–∞–≤–∂–¥–∏ 1)
    const currentPage = 1;

    // –§–æ—Ä–º—É—î–º–æ URL –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è 12 –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑—ñ –∑–º—ñ—â–µ–Ω–Ω—è–º –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ –¥–æ —Å—Ç–æ—Ä—ñ–Ω–∫–∏ (skip = 0)
    const API_URL_PRODUCTS = `https://dummyjson.com/products?limit=12&skip=${(currentPage - 1) * 12}`;

    try {
        // –í–∏–∫–æ–Ω—É—î–º–æ GET-–∑–∞–ø–∏—Ç –¥–æ API
        const response = await axios.get(API_URL_PRODUCTS);

        // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ —Ç—ñ–ª—å–∫–∏ –º–∞—Å–∏–≤ –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ —ñ–∑ –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ
        return response.data.products;

    } catch (error) {
        // –Ø–∫—â–æ —Å—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ ‚Äî –≤–∏–≤–æ–¥–∏–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è
        console.error('–ü–æ–º–∏–ª–∫–∞ –ø—ñ–¥ —á–∞—Å –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤:', error.message);

        // –ü–µ—Ä–µ–¥–∞—î–º–æ –ø–æ–º–∏–ª–∫—É –¥–∞–ª—ñ
        throw error;
    }
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑–∞ ID
export async function fetchProductById(id) {
  const response = await fetch(`https://dummyjson.com/products/${id}`);
  if (!response.ok) throw new Error('Failed to fetch product');
  return await response.json();
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–æ—à—É–∫—É –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑–∞ –Ω–∞–∑–≤–æ—é
export async function searchProductsByName(query) {
  const API_URL = `https://dummyjson.com/products/search?q=${encodeURIComponent(query)}&limit=12`;

  try {
    const response = await axios.get(API_URL);
    return response.data.products;
  } catch (error) {
    console.error('–ü–æ–º–∏–ª–∫–∞ –ø—ñ–¥ —á–∞—Å –ø–æ—à—É–∫—É –ø—Ä–æ–¥—É–∫—Ç—ñ–≤:', error.message);
    throw error;
  }
}
