import { openModal } from './modal.js';
import { cards } from './storage.js';
import { refs } from './refs.js';

// –ü–æ–∫–∞–∑ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è "–Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ"
export function showNotFoundMessage() {
    const notFound = document.querySelector('.not-found');
    if (notFound) {
      notFound.classList.add('not-found--visible');
    }
  }
// –°—Ö–æ–≤–∞—Ç–∏ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è "–Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ" 
  export function hideNotFoundMessage() {
    const notFound = document.querySelector('.not-found');
    if (notFound) {
      notFound.classList.remove('not-found--visible');
    }
  }  
  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∫–Ω–æ–ø–∫–∏ 
export function showLoadMoreBtn() {
    if (refs.loadMoreBtn) {
        refs.loadMoreBtn.classList.remove('is-hidden');
        refs.loadMoreBtn.classList.add('is-visible');
    }
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è –∫–Ω–æ–ø–∫–∏ 
export function hideLoadMoreBtn() {
    if (refs.loadMoreBtn) {
        refs.loadMoreBtn.classList.add('is-hidden');
        refs.loadMoreBtn.classList.remove('is-visible');
    }
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –ª–æ–∞–¥–µ—Ä–∞
export function showLoader() {
    const loader = document.querySelector('.loader');
    if (loader) {
      loader.classList.remove('is-hidden');
      loader.classList.add('is-visible');
    }
  }
  // –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è –ª–æ–∞–¥–µ—Ä–∞
  export function hideLoader() {
    const loader = document.querySelector('.loader');
    if (loader) {
      loader.classList.add('is-hidden');
      loader.classList.remove('is-visible');
    }
  }
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—á–∏—â–µ–Ω–Ω—è —Ä–æ–∑–º—ñ—Ç–∫–∏ –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ —Ç–∞ –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –ª–æ–∞–¥–µ—Ä–∞
export function showPageLoader() {
  document.querySelectorAll('.loader-page').forEach(loader => {
    loader.classList.remove('is-hidden');
    loader.classList.add('is-visible');
  });
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è –ª–æ–∞–¥–µ—Ä–∞
export function hidePageLoader() {
  document.querySelectorAll('.loader-page').forEach(loader => {
    loader.classList.add('is-hidden');
    loader.classList.remove('is-visible');
  });
}
// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –∫–ª—ñ–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∫—É –ø—Ä–æ–¥—É–∫—Ç—É
export function onProductCardClick(e) {
    const card = e.target.closest('.products__item');
    if (!card) return;
  
    const productId = card.dataset.id;
    const product = cards.find(p => p.id === Number(productId));
  
    if (product) {
      openModal(product);
    }
}
  
